/**  Store */
export const Satisfaction = {
  namespaced: true,
  state: () => ({
    /**    */
    code: [],
    /**     */
    defaultScore: '',
  }),
  mutations: {
    SET_SATISFACTION(state, code) {
      state.code = code;
    },
  },
  actions: {
    FETCH_CODE({ commit }, values) {
      commit('SET_SATISFACTION', values);
    },
  },
};
